.pir_rg {
    font-family: "pirulen_rg";
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.google_btn {
    height: 55px;
    border-radius: 6px;
    border: 1px solid #3e5be0;
    cursor: pointer;
    font-family: "pirulen_rg";

    font-size: 14px;
}
.google_btn > i {
    margin-right: 4px;
}

.help_view {
    font-size: 12px;
    color: #079688;
    margin-bottom: 24px;
    cursor: pointer;
}

.help_view > i {
    margin-right: 6px;
}

.resend-btn {
    border: none;
    color: #009688;
    background: transparent;
}

input {
    font-weight: initial !important;
}

.password_title {
    text-align: left;
    color: #f7f8ff;
    margin-bottom: 20px;
}
